<template>
  <div class="messagecenter">
    <div>
      <mt-header>
        <a slot="left" @click="back">
          <mt-button icon="back">通知</mt-button>
        </a>
      </mt-header>
    </div>
    <div class="messagecenterBody">
      <div v-for="message in center.items" @click="messageDetail(message.id)">
        <a class="w_cell" >
          <div class="w_cell_wraper">
            <div class="w_cell_title">
              <span class="w_cell_text" v-if="message.isRead"><img src="../assets/read.png" alt=""></span>
              <span class="w_cell_text" v-else><img src="../assets/noread.png" alt=""></span>
            </div>
            <div class="w_cell_value">
              <span>{{message.title}}</span> 
            </div>
          </div>
        </a>
        <!-- <mt-cell v-for="message in center.items" :title="message.title" @click.native="messageDetail(message.id)"></mt-cell> -->
      </div>
    </div>
    <div class="msgbox" v-if="show">
      <div class="m_header">
        <div class="m_title">提示</div>
      </div>
      <div class="m_content">
        <div class="m_message">
          {{message}}
        </div>
      </div>
      <div class="m_btns">
        <button @click="handleCancle" class="btn btn_cancel">取消</button>
        <button @click="handleRight" class="btn btn_right">确定</button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import { MessageBox } from 'mint-ui'
export default {
  name: 'messageCenter',
  computed: {
    ...mapGetters({
      center:'getMessageCenter'
    })
  },
  data() {
    return {
      show: false,
      message: '',
      id: ''
    }
  },
  methods: {
    ...mapActions({
      getMessageCenter: 'getMessageCenter',
      changeTabSelected: 'changeTabSelected',
      getDetailMessage: 'getDetailMessage',
      readMessage: 'readMessage'
    }),
    back: function() {
      this.$router.push({path: '/index/mymenu'});
      //this.changeTabSelected('我的');
    },
    handleCancle: function() {
      this.show = false;
    },
    handleRight: function() {
      this.readMessage(this.id).then(response => {
        if(response.ok) {
          this.show = false;
          this.getMessageCenter();
        }
      })
    },
    messageDetail: function(id) {
      this.getDetailMessage(id).then(response => {
        this.message = response.data.content;
        this.show = true;
        this.id = id;
      })
    }
  },
  created(){
    this.getMessageCenter();
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.messagecenter .m_btns .btn.btn_right {
      color: #d06091;
}
.messagecenter .m_message {
  font-size: 0.3rem;
}
.messagecenter .w_cell_value {
  border-bottom: none;
}
.messagecenter .w_cell_title {
  flex: 0;
  margin-right: 0.3rem;
}
.messagecenter .messagecenterBody {
  padding: 0.3rem;
}
</style>



